<template>
    <b-card>
        <b-row>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableDateFrom"
                        v-model="valueFromCheck"
                        :content="fromLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="4">
                <TimePickerFilter
                        :value="valueFrom"
                        @updateFilter="updateFilterFromDate"
                        :disabled="this.valueFromCheck === false"
                ></TimePickerFilter>
            </b-col>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableDateTo"
                        v-model="valueToCheck"
                        :content="toLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="4">
                <TimePickerFilter
                        :value="valueTo"
                        @updateFilter="updateFilterToDate"
                        :disabled="this.valueToCheck === false"
                ></TimePickerFilter>
            </b-col>
        </b-row>
        <b-row>
            <b-col cols="2">
                <EnableCheckbox
                        @change="updateFilterRunning"
                        v-model="valueRunning"
                        :content="runningLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableState"
                        v-model="valueStateCheck"
                        :content="stateLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="3">
                <DropboxFilter
                        :valueSelected="valueState"
                        :options="optionsState"
                        :disabled="this.valueStateCheck === false"
                        @input="updateFilterState"/>
            </b-col>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableSort"
                        v-model="valueSortCheck"
                        :content="sortLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="3">
                <DropboxFilter
                        :valueSelected="valueSort"
                        :options="optionsSort"
                        :disabled="this.valueSortCheck === false"
                        @input="updateFilterSort"/>
            </b-col>
        </b-row>
        <b-row>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableDateModified"
                        v-model="valueModifiedCheck"
                        :content="modifiedSinceLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="4">
                <TimePickerFilter
                        :value="valueModified"
                        @updateFilter="updateFilterModifiedDate"
                        :disabled="this.valueModifiedCheck === false"
                ></TimePickerFilter>
            </b-col>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableLimit"
                        v-model="valueLimitCheck"
                        :content="limitLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="3">
                <b-form-input v-model="valueLimit"
                              placeholder="Enter the limit"
                              style="font-size: 0.75rem !important;"
                              :disabled="this.valueLimitCheck === false"
                              type="text"
                              @change="updateFilterLimit"
                ></b-form-input>
            </b-col>
        </b-row>
        <b-row>
            <b-col cols="2">
                <EnableCheckbox
                        @change="enableTypes"
                        v-model="valueTypeCheck"
                        :content="typeLabel"
                ></EnableCheckbox>
            </b-col>
            <b-col cols="2">
                <b-button
                        :disabled="this.valueTypeCheck === false"
                        id="submitBtn"
                        @click="$bvModal.show('modal-scoped')"
                        style="font-size: 0.75rem;padding:0.25rem"
                >Modify Types
                </b-button>
            </b-col>
            <ModalFilter
                    :title = "modalTitle"
                    :label="typesMessage"
                    :value="selected"
                    :options="optionsTypes"
                    @listchange="setSelected"
                    @clickOk = "setTypeFilters"
                    @clickCancel = "cancelTypeFilters"
            ></ModalFilter>
        </b-row>
    </b-card>
</template>

<script>
    import {mapState} from 'vuex';
    import EnableCheckbox from "./SingleCheckbox";
    import DropboxFilter from "./DropboxFilter"
    import TimePickerFilter from "./TimePickerFilter";
    import ModalFilter from "./ModalFilter";

    export default {
        name: "FilterPanel",
        components: {ModalFilter, TimePickerFilter, EnableCheckbox, DropboxFilter},
        computed: {
            updatedFilters: {
                get() {
                    return this.$store.state.filters
                },
                set(value) {
                    console.log("update filters");
                    console.log(value);
                    this.$store.commit('UPDATE_FILTERS', value)
                }
            },
            ...mapState(['types']),
            optionsTypes() {
                var options = [];
                for (let item of this.types) {
                    var payload = {};
                    payload["text"] = item;
                    payload["value"] = item;
                    options.push(payload);
                }
                console.log("options array")
                console.log(options)
                return options;
            },
            typesMessage() {
                if (this.types.size > 0)
                    return "Select multiple types to filter"
                else
                    return "You have selected no endpoints to view"
            },
/*            valueFromCheck:{
                get(){
                    return this.fromLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            },
            valueToCheck:{
                get(){
                    return this.toLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            },
            valueStateCheck:{
                get(){
                    return this.stateLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            },

            valueSortCheck:{
                get(){
                    return this.sortLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            },

            valueModifiedCheck:{
                get(){
                    return this.modifiedSinceLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            },
            valueLimitCheck:{
                get(){
                    return this.limitLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            },
            valueTypeCheck:{
                get(){
                    return this.typeLabel in this.updatedFilters;
                },
                set(value){
                    console.log(value)
                }
            }*/
        },
        created(){
            this.valueFromCheck = false;
        },
        data() {
            return {
                valueFrom: '',
                valueTo: '',
                valueRunning: false,
                valueState: null,
                valueSort: null,
                valueModified: '',
                valueLimit: '',
                optionsState: [
                    {value: null, text: 'Select the state'},
                    {value: 'default', text: 'DEFAULT'},
                    {value: 'trashed', text: 'TRASHED'},
                    {value: 'all', text: 'ALL'},
                ],
                optionsSort: [
                    {value: null, text: 'Select the sorting order'},
                    {value: true, text: 'TRUE'},
                    {value: false, text: 'FALSE'}
                ],
                modalShow: false,
                selected: [], // Must be an array reference!
                fromLabel: "From",
                toLabel: "To",
                stateLabel: "State",
                sortLabel: "Sort",
                modifiedSinceLabel: "Modified Since",
                limitLabel: 'Limit',
                typeLabel: 'Types',
                runningLabel: 'Running',
                valueFromCheck:false,
                valueToCheck:false,
                valueStateCheck:false,
                valueSortCheck:false,
                valueModifiedCheck:false,
                valueLimitCheck:false,
                valueTypeCheck:false,
                //modal data
                modalTitle : "Select Types to Filter"
            }
        },
        methods: {
            setSelected(value)
            {
                this.selected = value
            },
            updateFilterFromDate(ctx) {
                this.valueFrom = ctx.selectedYMD
                if (this.valueFrom != "") {
                    let datum = Date.parse(this.valueFrom);
                    this.updateFilters(this.fromLabel, datum / 1000)
                } else {
                    this.removeFilterAttr(this.fromLabel);
                }
            },
            updateFilterToDate(ctx) {
                this.valueTo = ctx.selectedYMD
                if (this.valueTo != "") {
                    let datum = Date.parse(this.valueTo);
                    this.updateFilters(this.toLabel, datum / 1000)
                } else {
                    this.removeFilterAttr(this.toLabel)
                }
            },
            updateFilterRunning(value) {
                this.valueRunning = value;
                if (this.valueRunning === true)
                    this.updateFilters(this.runningLabel, true)
                else {
                    this.removeFilterAttr(this.runningLabel)
                }
            },
            updateFilterLimit(value) {
                if (value === "") {
                    this.removeFilterAttr(this.limitLabel);
                    return;
                }
                if (isNaN(value)) {
                    this.valueLimit = '';
                    return;
                }
                this.valueLimit = value;
                if (this.valueLimit != "")
                    this.updateFilters(this.limitLabel, parseInt(value))

            },
            updateFilterState(value) {
                this.valueState = value;
                if (this.valueState != null)
                    this.updateFilters(this.stateLabel, this.valueState)
                else
                    this.removeFilterAttr(this.stateLabel)
            },
            updateFilterSort(value) {
                this.valueSort = value;
                if (this.valueSort != null)
                    this.updateFilters(this.sortLabel, this.valueSort)
                else
                    this.removeFilterAttr(this.sortLabel)
            },
            updateFilterModifiedDate(ctx) {
                this.valueModified = ctx.selectedYMD
                if (this.valueModified != "") {
                    let datum = Date.parse(this.valueModified);
                    this.updateFilters(this.modifiedSinceLabel, datum / 1000)
                } else
                    this.removeFilterAttr(this.modifiedSinceLabel)

            },
            updateFilters(key, value) {
                var filteredData = Object.assign({}, this.updatedFilters);
                filteredData[key] = value;
                this.updatedFilters = Object.assign({}, filteredData)
            },
            removeFilterAttr(key) {
                var filteredData = Object.assign({}, this.updatedFilters);
                delete filteredData[key];
                this.updatedFilters = Object.assign({}, filteredData)
            },
            enableDateFrom(value) {
                if (value === false) {
                    this.valueFrom = '';
                }
            },
            enableDateTo(value) {
                if (value === false) {
                    this.valueTo = '';
                }

            },
            enableState(value) {
                if (value === false) {
                    this.valueState = null;
                }

            },
            enableSort(value) {
                if (value === false) {
                    this.valueSort = null;
                    //this.removeFilterAttr("sort");
                }

            },
            enableDateModified(value) {
                if (value === false) {
                    this.valueModified = '';
                    //this.removeFilterAttr("modifiedSince");
                }
            },
            enableLimit(value) {
                if (value === false) {
                    this.valueLimit = '';
                    this.removeFilterAttr(this.limitLabel)
                }
            },
            enableTypes(value) {
                if (value === false) {
                    this.selected = [];
                    this.removeFilterAttr(this.typeLabel)
                }
            },
            cancelTypeFilters(cancel) {
                cancel();
            },
            setTypeFilters(ok) {
                if (this.selected.length > 0) {
                    this.updateFilters(this.typeLabel, this.selected)
                } else {
                    this.removeFilterAttr(this.typeLabel)
                }
                ok();
            }
        }
    }
</script>

<style>
    .mt-3, .my-3 {
        margin-top: 0.5rem !important;
        font-size: 0.75rem !important;
    }

    .b-form-btn-label-control.form-control > label {
        font-size: 0.75rem !important;
    }

    .dropdown-item {
        font-size: 0.75rem !important;
    }

    .input-group > .input-group-append > .btn-group > .btn {
        font-size: 0.75rem !important;
        border-radius: 0;
    }

    .customized_div {
        padding: 0 !important;
    }
</style>